      </div>
      <div class="demo">
        <img class="an img" src="../assets/logo.jpg" width="150" height="150"/>
      </div>
      
      <van-button style="width:150px;margin-top: 30px;"  color="linear-gradient(to right, #6476CA, #ee0a24)" @click="show()" size="large">进入网站</van-button><br/>
      <van-button style="width:150px;margin-top: 30px;" color="linear-gradient(to right, #ee0a24, #6476CA,#ee0a24)" @click="zhu()"  type="primary"  size="large">进入主站</van-button><br/>
      <van-button style="width:150px;margin-top: 30px;" color="linear-gradient(to right, #ee0a24, #6476CA)" url="http://wpa.qq.com/msgrd?v=3&uin=3221965669&site=qq&menu=yes"  type="primary"  size="large">联系本人</van-button>
    <div style="width:100%;text-align: center;margin-top: 50px;color:white">如果遇到打不开了<br/>请复制链接到浏览器访问！</div>
  </div>
