<script>
import { Toast } from 'vant';
export default {
  data() {
    return {
      show1:true,
    }
  },
  methods: {
   show(){
     const toast = Toast.loading({
      duration: 0,
      forbidClick: true,
      message: '请稍等',
    });

    let second = 3;
    const timer = setInterval(() => {
      second--;
      if (second) {
        if(second == 3){
          toast.message = `请稍等.`;
        }else if(second == 2){
          toast.message = `请稍等..`;
        }else if(second == 1){
          toast.message = `请稍等...`;
        }
      } else {
        clearInterval(timer);
        Toast.clear();
        Toast({
          message: '欢迎回来！',
          icon: 'https://cdn.jsdelivr.net/npm/@vant/assets/logo.png',
          duration:1000
        });

        setInterval (function () {
          window.location.href="https://www.789ys.com";
        }, 1000); 
        this.show1=false;
      }
    }, 1000);
   },
   zhu(){
      Toast({
          message: '请稍等...',
          icon: 'https://cdn.jsdelivr.net/npm/@vant/assets/logo.png',
          duration:3500
        });
     setInterval (function () {
        window.location.href="https://des-yang.github.io/";
      }, 3500); 
   }
  },
  mounted() {
    
  }
}
</script>

